* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.6;
  color: #333;
  overflow-x: hidden;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

/* Header Styles */
.header {
  background: black;
  box-shadow: 0 2px 8px rgba(0,0,0,0.05);
  padding: 1rem 0;
  position: sticky;
  top: 0;
  z-index: 1000;
}

.header-inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1rem;
}

.logo {
  font-size: 1.4rem;
  font-weight: bold;
  color: white;
  text-decoration: none;
  transition: color 0.3s;
}

.logo:hover {
  color: #d4af37;
}

/* НАВИГАЦИЯ ПО УМОЛЧАНИЮ - ВИДИМА В СТРОКУ НА ДЕСКТОПЕ */
.nav {
  display: flex;
}

.nav ul {
  display: flex;
  list-style: none;
  gap: 2rem;
  margin: 0;
  padding: 0;
}

.nav li {
  margin: 0;
}

.nav a {
  text-decoration: none;
  color: #ffffff;
  font-weight: 500;
  transition: color 0.3s;
  position: relative;
  font-size: 1rem;
  white-space: nowrap;
}

.nav a:hover {
  color: #d4af37;
}

.nav a::after {
  content: '';
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 0;
  height: 2px;
  background: #d4af37;
  transition: width 0.3s;
}

.nav a:hover::after {
  width: 100%;
}

.header-actions {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.header-actions a {
  color: white;
  text-decoration: none;
  transition: color 0.3s;
  font-size: 1.2rem;
}

.header-actions a:hover {
  color: #d4af37;
}

.cart-count {
  background: #d4af37;
  color: white;
  border-radius: 50%;
  padding: 0.2rem 0.5rem;
  font-size: 0.7rem;
  margin-left: 0.2rem;
}

/* ГАМБУРГЕР СКРЫТ НА ДЕСКТОПЕ */
.menu-toggle {
  display: none;
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  color: white;
}

/* Hero Section */
.hero {
  background: linear-gradient(135deg, black, gray),
              url('//monxuan.ro/wp-content/plugins/revslider/public/assets/assets/dummy.png') no-repeat center center/cover;
  color: #fff;
  text-align: center;
  padding: 4rem 1rem;
  position: relative;
  min-height: 60vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.hero-content {
  max-width: 800px;
  margin: 0 auto;
}

.hero h1 {
  font-size: 2.2rem;
  margin-bottom: 1.2rem;
  font-weight: 700;
  text-shadow: 2px 2px 4px rgba(0,0,0,0.3);
  line-height: 1.2;
}

.hero p {
  font-size: 1.1rem;
  margin-bottom: 2rem;
  font-weight: 300;
  opacity: 0.95;
  line-height: 1.5;
}

.btn {
  display: inline-block;
  background: #fff;
  color: black;
  padding: 0.9rem 2rem;
  text-decoration: none;
  border-radius: 25px;
  font-weight: 600;
  transition: all 0.3s ease;
  border: 2px solid transparent;
  font-size: 0.95rem;
}

.btn:hover {
  background: transparent;
  color: #fff;
  border-color: #fff;
  transform: translateY(-2px);
}

/* Categories Section */
.categories {
  padding: 3rem 0;
  background: #f8f9fa;
}

.section-title {
  text-align: center;
  margin-bottom: 2rem;
  font-size: 2rem;
  font-weight: 600;
  color: #333;
  padding: 0 1rem;
}

.section-subtitle {
  text-align: center;
  margin-bottom: 2rem;
  color: #666;
  font-size: 1rem;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 1rem;
  line-height: 1.5;
}

.category-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.5rem;
}

.category-card {
  background: #fff;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 3px 15px rgba(0,0,0,0.1);
  text-align: center;
  padding: 2rem 1.5rem;
  transition: all 0.3s ease;
  cursor: pointer;
}

.category-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 25px rgba(0,0,0,0.15);
}

.category-icon {
  width: 70px;
  height: 70px;
  margin: 0 auto 1.2rem;
  background: #f8f9fa;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.8rem;
  color: black;
}

.category-card h3 {
  margin-bottom: 0.8rem;
  font-size: 1.2rem;
  font-weight: 600;
}

.category-card p {
  color: #666;
  font-size: 0.9rem;
  line-height: 1.4;
}

/* Featured Products */
.featured-products {
  padding: 3rem 0;
  background: #fff;
}

.product-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.5rem;
}

.product-card {
  background: #fff;
  border: 1px solid #eee;
  border-radius: 10px;
  overflow: hidden;
  text-align: center;
  padding: 1.5rem;
  transition: all 0.3s ease;
  position: relative;
}

.product-card:hover {
  transform: translateY(-3px);
  box-shadow: 0 5px 20px rgba(0,0,0,0.1);
}

.product-badge {
  position: absolute;
  top: 0.8rem;
  right: 0.8rem;
  background: black;
  color: white;
  padding: 0.3rem 0.7rem;
  border-radius: 15px;
  font-size: 0.7rem;
  font-weight: 600;
}

.product-image {
  width: 100%;
  height: 180px;
  object-fit: contain;
  margin-bottom: 1.2rem;
  transition: transform 0.3s ease;
}

.product-card:hover .product-image {
  transform: scale(1.03);
}

.product-title {
  font-weight: 600;
  margin-bottom: 0.5rem;
  font-size: 1.1rem;
  line-height: 1.3;
}

.product-description {
  color: #666;
  font-size: 0.85rem;
  margin-bottom: 0.8rem;
  line-height: 1.4;
}

.product-price {
  color: black;
  font-weight: bold;
  font-size: 1.2rem;
  margin-bottom: 1.2rem;
}

.add-to-cart {
  background: #333;
  color: #fff;
  border: none;
  padding: 0.8rem 1.2rem;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 500;
  width: 100%;
  font-size: 0.9rem;
  min-height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.add-to-cart:hover {
  background: black;
  transform: translateY(-2px);
}

/* Footer */
.footer {
  background: #2c3e50;
  color: #fff;
  padding: 3rem 0 1.5rem;
}

.footer-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
  margin-bottom: 2rem;
  padding: 0 1rem;
}

.footer-col h4 {
  margin-bottom: 1.2rem;
  color: #d4af37;
  font-size: 1.1rem;
  font-weight: 600;
}

.footer-col a {
  color: #bdc3c7;
  text-decoration: none;
  display: block;
  margin-bottom: 0.6rem;
  transition: color 0.3s;
  font-size: 0.9rem;
}

.footer-col a:hover {
  color: #d4af37;
}

.social-links {
  display: flex;
  gap: 0.8rem;
  margin-top: 1rem;
}

.social-links a {
  background: #34495e;
  width: 35px;
  height: 35px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  font-size: 1rem;
}

.social-links a:hover {
  background: #d4af37;
  transform: translateY(-2px);
}

.copyright {
  text-align: center;
  padding-top: 1.5rem;
  border-top: 1px solid #34495e;
  font-size: 0.8rem;
  color: #95a5a6;
  padding-left: 1rem;
  padding-right: 1rem;
}

/* Стили модального окна товара */
.product-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 1rem;
  backdrop-filter: blur(5px);
}

.product-modal {
  background: white;
  border-radius: 15px;
  max-width: 95vw;
  width: 100%;
  max-height: 90vh;
  overflow-y: auto;
  position: relative;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  animation: modalAppear 0.3s ease-out;
}

@keyframes modalAppear {
  from {
    opacity: 0;
    transform: scale(0.9) translateY(20px);
  }
  to {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

.modal-close-btn {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  background: rgba(0, 0, 0, 0.7);
  color: white;
  border: none;
  width: 35px;
  height: 35px;
  border-radius: 50%;
  font-size: 1.2rem;
  cursor: pointer;
  z-index: 10;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-close-btn:hover {
  background: #e74c3c;
  transform: scale(1.05);
}

.modal-content {
  display: grid;
  grid-template-columns: 1fr;
  gap: 0;
}

.modal-image {
  position: relative;
  height: 250px;
  overflow: hidden;
}

.modal-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.modal-info {
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}

.modal-info h2 {
  font-size: 1.5rem;
  color: #333;
  font-weight: 700;
  margin-bottom: 0.5rem;
  line-height: 1.3;
}

.modal-description {
  color: #666;
  line-height: 1.5;
  font-size: 0.9rem;
}

.modal-price {
  display: flex;
  align-items: center;
  gap: 1rem;
  flex-wrap: wrap;
  padding: 1rem 0;
  border-top: 1px solid #f0f0f0;
  border-bottom: 1px solid #f0f0f0;
}

.modal-price .current-price {
  font-size: 1.5rem;
}

.modal-price .original-price {
  font-size: 1.1rem;
}

.modal-price .discount-percent {
  font-size: 0.8rem;
  padding: 0.2rem 0.6rem;
}

.product-features h3,
.product-usage h3 {
  font-size: 1.1rem;
  color: #333;
  margin-bottom: 0.8rem;
  font-weight: 600;
}

.product-features ul {
  list-style: none;
  padding: 0;
}

.product-features li {
  padding: 0.4rem 0;
  color: #555;
  border-bottom: 1px solid #f8f8f8;
  font-size: 0.9rem;
}

.product-features li:last-child {
  border-bottom: none;
}

.product-usage p {
  color: #666;
  line-height: 1.5;
  background: #f8f9fa;
  padding: 0.8rem;
  border-radius: 6px;
  border-left: 4px solid #d4af37;
  font-size: 0.9rem;
}

.modal-actions {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
  margin-top: auto;
  padding-top: 1.5rem;
  border-top: 1px solid #f0f0f0;
  width: 100%;
}

.contact-btn {
  background: #d4af37;
  color: white;
  border: none;
  padding: 0.9rem 1.5rem;
  border-radius: 6px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  width: 100%;
  text-align: center;
  min-height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.contact-btn:hover {
  background: #b8941f;
  transform: translateY(-2px);
}

.close-btn {
  background: #f8f9fa;
  color: #333;
  border: 1px solid #e0e0e0;
  padding: 0.9rem 1.5rem;
  border-radius: 6px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  width: 100%;
  text-align: center;
  min-height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.close-btn:hover {
  background: #e9ecef;
  border-color: #d4af37;
}

/* Специальные стили для кнопок чтобы не съезжали */
.cta-buttons {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.btn {
  display: inline-block;
  padding: 0.9rem 2rem;
  border-radius: 25px;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
  border: 2px solid transparent;
  cursor: pointer;
  text-align: center;
  width: 100%;
  max-width: 250px;
  font-size: 0.95rem;
  box-sizing: border-box;
  min-height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Фикс для кнопок карусели */
.carousel-button {
  background: black;
  color: white;
  border: none;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-size: 1.2rem;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
  flex-shrink: 0;
  min-height: 44px;
  min-width: 44px;
}

/* Фикс для социальных ссылок */
.social-link {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.6rem;
  padding: 0.8rem 1rem;
  border-radius: 8px;
  text-decoration: none;
  color: #333;
  transition: all 0.3s ease;
  border: 1px solid #f0f0f0;
  font-weight: 500;
  background-color: #fff;
  width: 100%;
  font-size: 0.9rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  min-height: 44px;
}

/* МОБИЛЬНЫЕ СТИЛИ */
@media (max-width: 768px) {
  .menu-toggle {
    display: block;
  }

  /* НА МОБИЛЬНЫХ НАВИГАЦИЯ СКРЫТА */
  .nav {
    position: fixed;
    top: 70px;
    left: -100%;
    width: 100%;
    background: #fff;
    box-shadow: 0 5px 10px rgba(0,0,0,0.1);
    transition: left 0.3s ease;
    z-index: 999;
    display: block;
  }

  .nav.active {
    left: 0;
  }

  .nav ul {
    flex-direction: column;
    padding: 1rem;
    gap: 0;
  }

  .nav li {
    text-align: center;
    padding: 1rem;
    border-bottom: 1px solid #eee;
    width: 100%;
  }

  .nav a {
    color: #333;
    font-size: 1.1rem;
    display: block;
    width: 100%;
  }

  .nav a::after {
    display: none;
  }

  .header-inner {
    padding: 0 1rem;
  }

  .container {
    padding: 0 2rem;
  }

  .hero {
    padding: 6rem 2rem;
    min-height: 70vh;
  }

  .hero h1 {
    font-size: 3rem;
  }

  .hero p {
    font-size: 1.2rem;
  }

  .category-grid {
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 2rem;
  }

  .product-grid {
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 2rem;
  }

  .footer-grid {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 3rem;
  }

  .modal-content {
    grid-template-columns: 1fr 1fr;
  }

  .modal-image {
    height: 400px;
  }

  .modal-info {
    padding: 2rem;
  }

  .modal-actions {
    flex-direction: row;
  }
}

@media (min-width: 1024px) {
  .hero h1 {
    font-size: 3.5rem;
  }

  .section-title {
    font-size: 2.5rem;
  }

  .modal-content {
    grid-template-columns: 1fr 1fr;
  }

  .modal-image {
    height: 500px;
  }
}

/* Специальные медиа-запросы для очень маленьких экранов */
@media (max-width: 380px) {
  .header-inner {
    padding: 0 0.5rem;
  }
  
  .logo {
    font-size: 1.2rem;
  }
  
  .header-actions {
    gap: 0.5rem;
  }
  
  .header-actions a {
    font-size: 1rem;
  }
  
  .btn {
    padding: 0.8rem 1.5rem;
    font-size: 0.9rem;
    max-width: 200px;
  }
  
  .hero h1 {
    font-size: 1.8rem;
  }
  
  .hero p {
    font-size: 1rem;
  }
}

@media (max-width: 320px) {
  .container {
    padding: 0 0.5rem;
  }
  
  .logo {
    font-size: 1.1rem;
  }
  
  .header-actions a {
    font-size: 0.9rem;
  }
  
  .btn {
    padding: 0.7rem 1.2rem;
    font-size: 0.85rem;
    max-width: 180px;
  }
  
  .hero {
    padding: 3rem 0.5rem;
    min-height: 50vh;
  }
  
  .hero h1 {
    font-size: 1.6rem;
  }
  
  .hero p {
    font-size: 0.9rem;
  }
  
  .add-to-cart {
    padding: 0.7rem 1rem;
    font-size: 0.85rem;
  }
  
  .category-card,
  .product-card {
    padding: 1.5rem 1rem;
  }
  
  .modal-info {
    padding: 1rem;
  }
  
  .modal-info h2 {
    font-size: 1.3rem;
  }
  
  .nav ul {
    gap: 0;
  }
  
  .nav li {
    padding: 0.8rem;
  }
}