.about {
  padding: 3rem 0;
  background: linear-gradient(135deg, #f8f9fa 0%, #ffffff 100%);
}

.about-header {
  text-align: center;
  margin-bottom: 2rem;
  padding: 0 1rem;
}

.about-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

/* Основной контент */
.about-main {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
  align-items: start;
  margin-bottom: 3rem;
}

.about-text h3 {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 1.2rem;
  font-weight: 600;
}

.about-text p {
  color: #666;
  line-height: 1.6;
  margin-bottom: 1.2rem;
  font-size: 1rem;
}

.about-story,
.about-mission,
.about-meaning {
  margin-bottom: 2rem;
}

.about-image {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.about-image img {
  width: 100%;
  height: auto;
  border-radius: 12px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.about-image img:hover {
  transform: translateY(-3px);
}

/* CTA секция */
.about-cta {
  background: linear-gradient(135deg, black 50%, white 100%);
  color: white;
  padding: 2rem 1rem;
  border-radius: 15px;
  text-align: center;
  box-shadow: 0 10px 25px rgba(212, 175, 55, 0.3);
}

.about-cta h3 {
  font-size: 1.6rem;
  margin-bottom: 1rem;
  font-weight: 600;
}

.about-cta p {
  font-size: 1rem;
  margin-bottom: 2rem;
  opacity: 0.95;
  line-height: 1.5;
}

.cta-buttons {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: center;
  align-items: center;
}

.btn {
  display: inline-block;
  padding: 0.9rem 2rem;
  border-radius: 25px;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
  border: 2px solid transparent;
  cursor: pointer;
  text-align: center;
  width: 100%;
  max-width: 250px;
  font-size: 0.95rem;
}

.btn-primary {
  background: white;
  color: #d4af37;
}

.btn-primary:hover {
  background: transparent;
  color: white;
  border-color: white;
  transform: translateY(-2px);
}

.btn-secondary {
  background: transparent;
  color: white;
  border-color: white;
}

.btn-secondary:hover {
  background: white;
  color: #d4af37;
  transform: translateY(-2px);
}

/* Tablet and Desktop */
@media (min-width: 768px) {
  .about {
    padding: 4rem 0;
  }

  .about-header {
    margin-bottom: 3rem;
  }

  .about-main {
    grid-template-columns: 1fr 1fr;
    gap: 3rem;
    margin-bottom: 4rem;
  }

  .about-text h3 {
    font-size: 1.7rem;
  }

  .about-text p {
    font-size: 1.05rem;
  }

  .cta-buttons {
    flex-direction: row;
  }

  .btn {
    width: auto;
  }

  .about-cta {
    padding: 3rem 2rem;
  }

  .about-cta h3 {
    font-size: 2rem;
  }
}

@media (min-width: 1024px) {
  .about {
    padding: 5rem 0;
  }

  .about-main {
    gap: 4rem;
  }

  .about-text h3 {
    font-size: 1.8rem;
  }

  .about-cta {
    padding: 4rem 2rem;
  }

  .about-cta h3 {
    font-size: 2.2rem;
  }

  .about-cta p {
    font-size: 1.1rem;
  }
}

/* Mobile Small Screens */
@media (max-width: 320px) {
  .about {
    padding: 2rem 0;
  }

  .about-header {
    margin-bottom: 1.5rem;
  }

  .about-text h3 {
    font-size: 1.3rem;
  }

  .about-text p {
    font-size: 0.9rem;
  }

  .about-cta {
    padding: 1.5rem 0.8rem;
  }

  .about-cta h3 {
    font-size: 1.4rem;
  }

  .btn {
    padding: 0.8rem 1.5rem;
    font-size: 0.9rem;
  }
}