.featured-products {
  padding: 3rem 0;
  background: #f8f9fa;
}

.carousel-container {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

.carousel {
  position: relative;
  display: flex;
  align-items: center;
  background: white;
  border-radius: 15px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

.carousel-track-container {
  overflow: hidden;
  flex: 1;
  margin: 0 0.5rem;
}

.carousel-track {
  display: flex;
  transition: transform 0.5s ease-in-out;
}

.carousel-slide {
  min-width: 100%;
  padding: 1.5rem;
  box-sizing: border-box;
}

.product-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  background: white;
  padding: 1.5rem;
  border-radius: 12px;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.08);
  transition: all 0.3s ease;
}

.product-card:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.12);
}

.product-image-container {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
}

.product-image {
  width: 100%;
  max-width: 280px;
  height: auto;
  object-fit: cover;
  border-radius: 8px;
  transition: transform 0.3s ease;
}

.product-card:hover .product-image {
  transform: scale(1.03);
}

.product-info {
  flex: 1;
  text-align: center;
  width: 100%;
}

.product-badge {
  position: absolute;
  top: 0.8rem;
  left: 0.8rem;
  background: black;
  color: white;
  padding: 0.4rem 0.8rem;
  border-radius: 15px;
  font-size: 0.7rem;
  font-weight: 600;
  z-index: 2;
}

.product-title {
  font-size: 1.4rem;
  font-weight: 600;
  margin-bottom: 0.8rem;
  color: #333;
  line-height: 1.3;
}

.product-description {
  color: #666;
  font-size: 0.9rem;
  line-height: 1.5;
  margin-bottom: 1.2rem;
}

.product-price {
  color: black;
  font-weight: bold;
  font-size: 1.3rem;
  margin-bottom: 1.5rem;
}

.add-to-cart {
  background: #333;
  color: #fff;
  border: none;
  padding: 0.9rem 1.5rem;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 500;
  font-size: 0.9rem;
  min-width: 140px;
  width: 100%;
  max-width: 200px;
}

.add-to-cart:hover {
  background: black;
  transform: translateY(-2px);
}

.carousel-button {
  background: black;
  color: white;
  border: none;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-size: 1.2rem;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
}

.carousel-button:hover {
  background: gray;
  transform: scale(1.05);
}

.carousel-indicators {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
  margin-top: 1.5rem;
}

.indicator {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: none;
  background: #ddd;
  cursor: pointer;
  transition: all 0.3s ease;
}

.indicator.active {
  background: black;
  transform: scale(1.1);
}

.indicator:hover {
  background: #b8941f;
}

.view-details-btn {
  background: #d4af37;
  color: white;
  border: none;
  padding: 0.7rem 1.2rem;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 500;
  width: 100%;
  margin-top: 1rem;
  font-size: 0.9rem;
}

.view-details-btn:hover {
  background: #b8941f;
  transform: translateY(-2px);
}

.product-image-container {
  cursor: pointer;
}

/* Mobile First Responsive Design */
@media (max-width: 768px) {
  .featured-products {
    padding: 2rem 0;
  }

  .carousel-container {
    padding: 0 0.5rem;
  }

  .carousel-slide {
    padding: 1rem;
  }

  .product-card {
    padding: 1rem;
    gap: 1.5rem;
  }

  .product-title {
    font-size: 1.2rem;
  }

  .product-description {
    font-size: 0.85rem;
  }

  .product-price {
    font-size: 1.2rem;
  }

  .carousel-button {
    width: 35px;
    height: 35px;
    font-size: 1rem;
  }

  .carousel-track-container {
    margin: 0 0.3rem;
  }
}

@media (max-width: 480px) {
  .featured-products {
    padding: 1.5rem 0;
  }

  .carousel {
    border-radius: 10px;
  }

  .product-card {
    padding: 0.8rem;
    gap: 1rem;
  }

  .product-image {
    max-width: 200px;
  }

  .product-title {
    font-size: 1.1rem;
    margin-bottom: 0.5rem;
  }

  .product-description {
    font-size: 0.8rem;
    margin-bottom: 1rem;
  }

  .product-price {
    font-size: 1.1rem;
    margin-bottom: 1rem;
  }

  .add-to-cart {
    padding: 0.8rem 1.2rem;
    font-size: 0.85rem;
    min-width: 120px;
  }

  .carousel-indicators {
    margin-top: 1rem;
  }

  .indicator {
    width: 8px;
    height: 8px;
  }
}

@media (max-width: 320px) {
  .product-card {
    padding: 0.5rem;
  }

  .product-title {
    font-size: 1rem;
  }

  .product-image {
    max-width: 160px;
  }
}