/* Hero Section */
.hero {
  background: linear-gradient(135deg, #000000 0%, #2d2d2d 100%);
  color: #fff;
  padding: 2rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 70vh;
  position: relative;
  overflow: hidden;
  width: 100%;
}

.hero .container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hero-content {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  text-align: center;
}

.hero-image {
  max-width: 100%;
  height: auto;
  object-fit: contain;
  border-radius: 10px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s ease;
  display: block;
  margin: 0 auto;
}

/* Мобильные устройства */
@media (max-width: 768px) {
  .hero {
    min-height: 60vh;
    padding: 1.5rem 0;
  }
  
  .hero .container {
    padding: 0 0.5rem;
  }
  
  .hero-image {
    max-width: 95%;
    max-height: 50vh;
    width: auto;
  }
}

/* Маленькие телефоны */
@media (max-width: 480px) {
  .hero {
    min-height: 50vh;
    padding: 1rem 0;
  }
  
  .hero-image {
    max-width: 90%;
    max-height: 45vh;
  }
}

/* Очень маленькие телефоны */
@media (max-width: 320px) {
  .hero {
    min-height: 45vh;
    padding: 0.5rem 0;
  }
  
  .hero-image {
    max-width: 85%;
    max-height: 40vh;
  }
}

/* Планшеты */
@media (min-width: 769px) and (max-width: 1024px) {
  .hero-image {
    max-width: 80%;
    max-height: 60vh;
  }
}

/* Десктоп */
@media (min-width: 1025px) {
  .hero-image {
    max-width: 70%;
    max-height: 70vh;
  }
}

.hero-image:hover {
  transform: scale(1.02);
}
/* Принудительное центрирование если все еще съезжает */
.hero-content {
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
  width: 100% !important;
  margin: 0 !important;
  padding: 0 !important;
}

.hero-image {
  display: block !important;
  margin-left: auto !important;
  margin-right: auto !important;
  float: none !important;
}